@use 'variables' as v;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.container{
	max-width: 1170px;
	margin:auto;
}
.row{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
.footer{
	background-color: v.$marino;
    padding: 70px 0;
    margin-top: 250px;
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: v.$blanco;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: v.$beige;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: v.$blanco;
	text-decoration: none;
	font-weight: 300;
	color: v.$blanco;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: v.$blanco;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: v.$beige;
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: v.$blanco;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: v.$marino;
	background-color: v.$beige;
}

/*responsive*/
@media(max-width: 767px){
	.footer{
		background-color: v.$marino;
		padding: 70px 0;
		margin-top: 0px;
	}
  	.footer-col{
		width: 50%;
		margin-bottom: 30px;
	}
	.footer-col h4{
		font-size: 14px;
	}
	.footer-col ul li a{
		font-size: 12px;
	}
@media(max-width: 574px){

	.footer{
		background-color: v.$marino;
		padding: 70px 0;
		margin-top: 0px;
	}
	
}
}






